	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2012/7/the-verge-office-2013-preview/index.md absPath of file >>> MarkdownRemark","html":"<p>I like the <a href=\"http://www.theverge.com/2012/7/16/3156636/office-2013-screenshots-features-preview\">new look of Office 2013</a> and the “cloud” integration, but as The Verge reports:</p>\n<blockquote>\n<p>The touch experience isn’t great from my own testing on Windows 8 Release Preview, and it feels all too familiar to Windows 7 — a first stab at touch improvements on desktop software. Microsoft’s Office division has taken a similar approach, by increasing touch targets, but it seems that any truly touch-optimized versions of Office will come as Metro style apps, if anything at all. It’s clearly difficult to create a fully functional touch-based word processor or spreadsheet with legacy support, and Microsoft hasn’t cracked it yet.</p>\n</blockquote>","frontmatter":{"title":"The Verge: Office 2013 Preview","date":"July 16, 2012","passthroughUrl":null}}},"pathContext":{"slug":"/blog/2012/7/the-verge-office-2013-preview/","previous":{"fields":{"slug":"/blog/2012/7/google-galaxy-7-unboxing/"},"frontmatter":{"title":"Google Galaxy 7 Unboxing","passthroughUrl":null,"type":null}},"next":{"fields":{"slug":"/blog/2012/7/unsolicited-advice-for-marissa-mayer/"},"frontmatter":{"title":"Unsolicited Advice for Marissa Mayer","passthroughUrl":null,"type":null}}}}

